import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DegreeRequestsApi } from './api/degree-requests.api';
import { DegreeRequestData } from './interface/degree-requests';
import { DegreeRequestsService } from './services/degree-requests.service';
import { HttpClientModule, HttpClientXsrfModule } from '@angular/common/http';

const API = [
  DegreeRequestsApi,
];

const SERVICES = [
  { provide: DegreeRequestData, useClass: DegreeRequestsService },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    HttpClientModule,
    HttpClientXsrfModule.withOptions({
      cookieName: 'csrftoken',
      headerName: 'X-CSRFToken'
    })
  ]
})
export class CoreModule {
  static forRoot(): ModuleWithProviders<CoreModule> {
    return {
      ngModule: CoreModule,
      providers: [
        ...API,
        ...SERVICES,
      ],
    };
  }
}
