import { Observable } from 'rxjs';

export interface DegreeRequests {
    id: string,
    first_name: string,
    last_name: string,
    degree_name: string,
    email: string,
    created: Date,
    validated: Date,
    is_validated: boolean,
    rejected: Date,
    is_rejected: boolean,
    image: string
}

export interface DegreeRequestsLight {
    first_name: string,
    last_name: string,
    degree_name: string,
    email: string,
}

export interface OneTimePassword {
    id: string,
    password: string,
}

export interface DegreeRequestsCustom {
    otp: OneTimePassword,
    degree_requests: DegreeRequests[],
}

export abstract class DegreeRequestData {
    abstract create(degreeRequest: DegreeRequestsLight): Observable<DegreeRequestsLight>;
    abstract list(pageNumber: number, pageSize: number, isValidated: any, isRejected: any): Observable<DegreeRequests[]>;
    abstract getOTP(): Observable<OneTimePassword>;
    abstract update(degreeRequestsCustom: DegreeRequestsCustom): Observable<any>;
    abstract verify(image: any): Observable<any>;
}