import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DegreeRequestsApi } from '../api/degree-requests.api';
import { DegreeRequestData, DegreeRequests, DegreeRequestsCustom, DegreeRequestsLight, OneTimePassword } from '../interface/degree-requests';

@Injectable({
  providedIn: 'root'
})
export class DegreeRequestsService extends DegreeRequestData {

  constructor(private _api: DegreeRequestsApi) {
    super()
  }

  create(degreeRequest: DegreeRequestsLight): Observable<DegreeRequestsLight> {
    return this._api.add(degreeRequest);
  }

  list(pageNumber: number = 1, pageSize: number = 10, isValidated = '', isRejected = ''): Observable<DegreeRequests[]> {
    return this._api.list(pageNumber, pageSize, isValidated, isRejected);
  }

  getOTP(): Observable<OneTimePassword> {
    return this._api.getOTP();
  }

  update(degreeRequestsCustom: DegreeRequestsCustom): Observable<any> {
    return this._api.update(degreeRequestsCustom);
  }

  verify(image: any): Observable<any> {
    return this._api.verify(image);
  }
}
