import { HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpService } from './http.service';

@Injectable({
    providedIn: 'root'
})
export class DegreeRequestsApi {
    private readonly apiController: string = 'degree-requests';

    constructor(private _api: HttpService) { }

    list(pageNumber: number = 1, pageSize: number = 10, isValidated = '', isRejected = ''): Observable<any> {
        let offsetPage = (pageNumber - 1) * pageSize;
        const params = new HttpParams()
            .set('offset', `${offsetPage}`)
            .set('limit', `${pageSize}`)
            .set('is_validated', `${isValidated}`)
            .set('is_rejected', `${isRejected}`);

        return this._api.get(`${this.apiController}/list/`, { params });
    }

    add(item: any): Observable<any> {
        return this._api.post(`${this.apiController}/create/`, item);
    }

    getOTP(): Observable<any> {
        return this._api.get(`${this.apiController}/`);
    }

    update(item: any): Observable<any> {
        return this._api.put(`${this.apiController}/`, item);
    }

    verify(image: any): Observable<any> {
        return this._api.post(`${this.apiController}/verify/`, image);
    }
}
